html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  height: 100vh;
  background: linear-gradient(var(--bg-color-up) 35%, var(--bg-color-down));
  color: var(--text-color);
  font-family: "EB Garamond", serif;
}

h1,
h2,
h3 {
  line-height: 1.1;
  margin: 0;
  font-weight: 700;
  font-family: "Libre Bodoni", serif;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

button {
  cursor: pointer;
}

#toggleTheme {
  border: none;
  background: none;
}

.icon {
  cursor: pointer;
  font-size: 2rem;
  font-weight: 500;
  padding: 0.5em;
}

.fb {
  color: #0866ff;
}

.in {
  color: #0a66c2;
}

:root {
  --bg-color-up: #b88c67;
  --bg-color-down: #ebdaca;
  --text-color: #0f0504;
  --bg-color-dark-up: #4d3c2f;
  --bg-color-dark-down: #b88c67;
}